.register {
    height: 500px;
    margin-top: 80px;
}

section {
    /* fallback for old browsers */
    background: #fccb90;
    /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-linear-gradient(to right, #ee7724, #d8363a, #dd3675, #b44593);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    background: linear-gradient(to right, #ee7724, #d8363a, #dd3675, #b44593);
    min-height: 100vh;
}

.form-control {
    background: rgb(239, 235, 235);

    border-radius: 15px;

}

.form-control:focus {
    box-shadow: 0 0 5px 2px #ff0000;
    /* Replace #ff0000 with your desired color */
    border-color: red;
}

[data-tip] {
    position: relative;

}

[data-tip]:before {
    content: '';
    /* hides the tooltip when not hovered */
    display: none;
    content: '';
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    border-bottom: 5px solid #1a1a1a;
    position: absolute;
    top: 30px;
    left: 35px;
    z-index: 8;
    font-size: 0;
    line-height: 0;
    width: 0;
    height: 0;
}

[data-tip]:after {
    display: none;
    content: attr(data-tip);
    position: absolute;
    top: 35px;
    left: 0px;
    padding: 5px 8px;
    background: white;
    color: black;
    border-style: solid;
    border-color: black;
    z-index: 9;
    font-size: 0.95em;
    max-width: 100%;
    overflow-wrap: break-word;
}

[data-tip]:hover:before,
[data-tip]:hover:after {
    display: block;
}

.left {
    text-align: left;
}