body {
    /* fallback for old browsers */
    background: #fccb90;
    /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-linear-gradient(to right, #ee7724, #d8363a, #dd3675, #b44593);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    background: linear-gradient(to right, #ee7724, #d8363a, #dd3675, #b44593);
    min-height: 100vh;
}

::-webkit-scrollbar {
    width: 5px;
}

::-webkit-scrollbar-track {
    width: 5px;
    background: #f5f5f5;
}

::-webkit-scrollbar-thumb {
    width: 1em;
    background-color: #ddd;
    outline: 1px solid slategrey;
    border-radius: 1rem;
}

.text-small {
    font-size: 0.9rem;
}

.messages-box,
.chat-box {
    height: 550px;
    overflow-y: scroll;
    text-align: left;
}

.rounded-lg {
    border-radius: 0.5rem;
}

input::placeholder {
    font-size: 0.9rem;
    color: #999;
}

.add {
    background-color: lightgray;
}

.identifier {
    width: 450px;
    height: 50px;
}

.user {
    padding-top: 18px;
    padding-left: 10px;
    font-weight: bold;
    text-align: left;
    align-items: left;
}

.me {
    background-color: lightgrey;
    text-align: left;
    height: 70px;
}

.sender {
    margin-right: auto;
    background-color: #E9E9E9;
}

.reciever {
    margin-left: auto;
    background-color: #FA476B;
}